.sort {
  cursor: pointer;
  display: inline-block;
  position: relative;

  &__text {
    color: var(--color-primary_active);
  }

  &__dropdown {
    width: 220px;
    position: absolute;
    top: 100%;
    left: 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  .bx {
    transition: transform 0.2s ease-in-out;
  }

  &:hover .sort__dropdown {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  &.is-active .sort__dropdown {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  &:hover .bx {
    transform: rotate(180deg);
  }
}
