.radio {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding-left: 30px;
  transition: all 0.1s ease-in-out;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &__input {
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    transition: all 0.2s ease-in-out;
  }

  &__dot {
    cursor: pointer;
    display: block;
    width: 20px;
    height: 20px;
    background-color: #fff;
    border: 1.4px solid #d3d3d3;
    border-radius: 50%;
    position: absolute;
    margin-left: -30px;
  }

  &__label {
    font-size: 16px;
    line-height: 1.2;
    color: var(--color-body);
  }

  .radio__num {
    color: var(--color-text-gray);
  }
}

// Checked
.radio__input:checked + .radio__dot {
  background: #fff
    url("data:image/svg+xml,%3Csvg%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Ccircle%20id%3D%22e081f0381900d4121b74b2d6d1debc53%22%20cx%3D%226%22%20cy%3D%226%22%20r%3D%226%22%20fill%3D%22%2300BA35%22%3E%3C%2Fcircle%3E%0A%3C%2Fsvg%3E")
    no-repeat center;
  background-size: 12px 12px;
  border: 1px solid var(--color-primary);
}

// Focused
.radio__input:focus + .radio__dot {
  outline: 1.5px solid rgba(0, 186, 53, 0.6);
}

.radio__input:checked:focus + .radio__dot {
  outline: 1.5px solid rgba(0, 186, 53, 0.6);
}

// Disabled
.radio__input:disabled + .radio__dot {
  background: #f0f0f0;
  border: 1px solid #dbdbdb;
}

.radio__input:checked:disabled + .radio__dot {
  background: #f0f0f0
    url("data:image/svg+xml,%3Csvg%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Ccircle%20id%3D%22e081f0381900d4121b74b2d6d1debc53%22%20cx%3D%226%22%20cy%3D%226%22%20r%3D%226%22%20fill%3D%22%23BFBFBF%22%3E%3C%2Fcircle%3E%0A%3C%2Fsvg%3E")
    no-repeat center;
  background-size: 12px 12px;
  border: 1px solid #dbdbdb;
}

.radio--disabled .radio__label {
  opacity: 0.7;
}
