// блок с фильтрами
.filters {
  padding-bottom: 32px;

  &__mclose {
    display: none;
  }

  &__mopen {
    height: 44px;
    line-height: 44px;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 24px;
    display: none;
    .bx {
      margin-right: 8px;
    }
  }

  &__title {
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 16px;
  }

  &__btns {
    display: flex;
    flex-direction: column;
    padding-top: 16px;
  }
  &__btn-podbor {
    margin-bottom: 12px;

    &.is-active {
      background: var(--color-primary);
      color: #fff;
      &:hover {
        background: var(--color-primary_active);
        border-color: transparent;
        color: #fff;
      }
    }
  }
  &__btn-clear {
    border-color: var(--color-border-dark);
    color: var(--color-text-gray);
  }
}

// фильтр
.filter {
  display: block;
  background: #f9f9fb;
  border-radius: 8px;
  padding: 24px;
  position: relative;

  &:not(:last-child) {
    margin-bottom: 16px;
  }

  &__arrow {
    cursor: pointer;
    display: inline-block;
    width: 24px;
    height: 24px;
    background: url("data:image/svg+xml,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20viewBox%3D%220%200%2020%2020%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cg%20id%3D%22b04319d90521d39c355a698aee3ead59%22%20opacity%3D%220.6%22%3E%0A%20%20%20%20%3Cpath%20id%3D%223623972492484c6002eb969c9ea7fcac%22%20d%3D%22M5.24414%208.92251L6.42247%207.74418L9.99997%2011.3217L13.5775%207.74418L14.7558%208.92251L9.99997%2013.6783L5.24414%208.92251Z%22%20fill%3D%22%23333333%22%3E%3C%2Fpath%3E%0A%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E")
      no-repeat center;
    background-size: cover;
    position: absolute;
    top: 12px;
    right: 12px;
    transition: all 0.2s ease-in-out;
    transform: rotate(-180deg);
    opacity: 0.8;
  }

  &__name {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin: 0;
  }

  &__list {
    margin-top: 16px;
  }

  .param-hidden {
    display: none;
    &.is-active {
      display: block;
    }
  }
}

// фильтр цены
.filter-price {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 8px;
  margin-top: 16px;
}
.price-inputs {
  &__box {
    position: relative;
  }
}

.price-label {
  display: inline-block;
  font-weight: 500;
  font-size: 14px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 16px;
  z-index: 10;
}

.price-input {
  width: 100%;
  height: 48px;
  padding-left: 36px;
  @extend .form__input;

  .price-start {
    border: none;
  }
}

// состояние закрытого фильтра
.filter--closed {
  padding: 16px;

  .filter__list {
    display: none;
  }

  .filter__price {
    display: none;
  }

  .filter__arrow {
    top: 16px;
    transform: rotate(0);
  }
}

@media (max-width: 1024px) {
  .filters {
    width: 100%;
    height: 100%;
    max-width: 320px;
    padding: 24px 0;
    background: #f9f9fb;
    border-radius: 0;
    box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.16),
      16px -16px 32px rgba(0, 0, 0, 0.16);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    @include trans-block;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;

    &.is-active {
      opacity: 1;
      pointer-events: auto;
      visibility: visible;

      .filters__mscroll {
        height: 80%;
        overflow-y: auto;
        // стилизация скрола
        &::-webkit-scrollbar {
          width: 4px; /* ширина scrollbar */
        }
        &::-webkit-scrollbar-track {
          background: rgba(0, 0, 0, 0.1); /* цвет дорожки */
        }
        &::-webkit-scrollbar-thumb {
          background: rgba(0, 0, 0, 0.1); /* цвет плашки */
          border-radius: 100px; /* закругления плашки */
          border: 2px solid var(--color-primary); /* padding вокруг плашки */
        }
      }
    }

    &__mopen {
      display: flex;
    }

    &__mclose {
      cursor: pointer;
      display: block;
      width: 32px;
      height: 32px;
      background: url("data:image/svg+xml,%3Csvg%20width%3D%2232%22%20height%3D%2232%22%20viewBox%3D%220%200%2032%2032%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cg%20id%3D%227737bfebbda146b65c810b0b1e2191df%22%20opacity%3D%220.5%22%3E%0A%20%20%20%20%3Cg%20id%3D%225157005ec15074153964f5ee56a62539%22%3E%0A%20%20%20%20%20%20%3Cpath%20id%3D%22f5b3093d8b16c7433a5b464a5182e74b%22%20d%3D%22M21.5893%208.45868L15.932%2014.1147L10.276%208.45868L8.39062%2010.344L14.0466%2016L8.39062%2021.656L10.276%2023.5413L15.932%2017.8853L21.5893%2023.5413L23.4746%2021.656L17.8186%2016L23.4746%2010.344L21.5893%208.45868Z%22%20fill%3D%22%23333333%22%3E%3C%2Fpath%3E%0A%20%20%20%20%3C%2Fg%3E%0A%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E")
        no-repeat center;
      background-size: cover;
      opacity: 0.8;
      position: absolute;
      top: 10px;
      right: 10px;
    }

    &__title {
      padding: 0 24px;
    }

    .filter {
      padding: 0 24px;
      border-radius: 0;
      background: none;
      padding-bottom: 24px;
      &:not(:last-child) {
        border-bottom: 1px solid var(--color-border-dark);
        margin-bottom: 24px;
      }

      &__arrow {
        top: 4px;
        right: 12px;
      }
    }

    &__btns {
      width: 100%;
      background: #fff;
      padding: 16px 24px;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}

@media (max-width: 480px) {
  .filters__mopen {
    margin-right: 0;
  }
}
