// --- стандартные сбросы
html {
  box-sizing: border-box;
  // scroll-behavior: smooth;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

// --- настройка body
body {
  background-color: #f5f5f5;
  min-width: 320px;
  margin: 0;
  font-family: var(--font-default);
  font-style: normal;
  font-size: var(--fs-base);
  font-weight: var(--fw-400);
  line-height: var(--body-lh);
  font-display: swap;
  overflow-x: hidden;
  color: var(--color-body);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  &.dis-scroll {
    overflow: hidden;
  }
}

// стилизация скрола
.scroll-line::-webkit-scrollbar {
  width: 4px; /* ширина scrollbar */
}
.scroll-line::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1); /* цвет дорожки */
}
.scroll-line::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1); /* цвет плашки */
  border-radius: 100px; /* закругления плашки */
  border: 2px solid var(--color-primary_active); /* padding вокруг плашки */
}

// --- прижатие футера к низу
.page__wrapper {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 1920px;
  margin: 0 auto;
}

.page__main {
  flex-grow: 1;
}
// ----- Настройка контейнера
.container {
  max-width: var(--container-width);
  padding: 0 var(--container-padding);
  margin: 0 auto;

  @media (max-width: 1024px) {
    padding: 0 24px;
  }

  @media (max-width: 767px) {
    padding: 0 16px;
  }
}

a {
  color: inherit;
  transition: all 0.2s ease-in-out;
}

ul {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  @include trans-btn;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  vertical-align: middle;
}

.is-hidden {
  display: none;
}

.is-active {
  display: block;
}

// сбросы стилей у элементов
.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.btn-reset {
  border: none;
  outline: none;
  background: none;
}

// Font Icons
.bx {
  display: inline-block;
  font-size: 20px;
  position: relative;
  vertical-align: middle;
  // top: -2px;
  &__left {
    margin-right: 6px;
  }
  &__right {
    margin-left: 6px;
  }
}

// Отступы
.mb16 {
  margin-bottom: 16px;
}

.mb32 {
  margin-bottom: 32px;
}

.pbt64 {
  padding: 64px 0;
}

// Section padding-bottom
.section-pb {
  padding-bottom: 64px;

  @media (max-width: 1024px) {
    padding-bottom: 48px;
  }

  @media (max-width: 480px) {
    padding-bottom: 32px;
  }
}

// Section padding: top-bottom
.section-ptb {
  padding: 64px 0 !important;

  @media (max-width: 1024px) {
    padding: 48px 0 !important;
  }

  @media (max-width: 480px) {
    padding: 32px 0 !important;
  }
}

.section-h2,
.page-title {
  color: var(--color-title);
  font-size: 36px;
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 32px;

  @media (max-width: 768px) {
    font-size: 32px;
    margin-bottom: 24px;
  }

  @media (max-width: 480px) {
    font-size: 28px;
    margin-bottom: 16px;
  }

  @media (max-width: 390px) {
    font-size: 24px;
  }

  @media (max-width: 375px) {
    font-size: 24px;
  }
}

.ic-dzen {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("../img/icons/ic-dzen.png") no-repeat center/cover;
  @include trans-link;
}

.bg-gray {
  background: #f5f5f5;
}

.card-flex {
  display: flex;
}
