.header {
  width: 100%;
  background: #fff;

  &-fixed {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: sticky;
    top: 0;
    height: auto;
    z-index: 1000;

    @media (max-width: 1280px) {
      border-bottom: 1px solid rgba(0, 0, 0, 0.08);
      box-shadow: 0 4px 24px 0 rgb(0 0 0 / 8%);
    }
  }

  &__top {
    width: 100%;
    background-color: #fff;
    padding: 8px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.14);

    &-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-icon {
      display: inline-block;
      font-size: 18px;
      line-height: 18px;
      color: var(--color-text-gray);
    }
  }

  &__address {
    width: 226px;
    display: flex;
    font-size: 14px;
    line-height: 20px;

    .header__top-icon {
      margin-right: 8px;
    }
  }

  &__search {
    width: 100%;
    padding: 16px 0;

    &-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &--box-1,
    &--box-2 {
      display: flex;
      align-items: center;
      flex-shrink: 0;
    }

    &--box-1 {
      flex-grow: 1;
      padding: 0 16px;
      padding-right: 24px;
    }

    .logo,
    .btn-catalog {
      flex-shrink: 0;
    }

    .form-search {
      width: 100%;
      margin-left: 16px;
      margin-right: 16px;
    }

    .btn-cart {
      flex-shrink: 0;
    }

    .phone {
      flex-shrink: 0;
      margin-right: 24px;
    }
  }
}

// Menu
.menu-top {
  &__list {
    display: flex;
    align-items: center;
    list-style: none;
  }

  &__item {
    display: inline-block;
  }

  &__link {
    display: inline-block;
    font-size: 14px;
    line-height: 20px;
    position: relative;
    padding: 0 12px;
    transition: all 0.3s ease-in-out;
    text-decoration: none;

    &:hover {
      color: var(--color-primary_active);
    }

    &:hover::after {
      width: 100%;
    }

    &::after {
      content: "";
      display: inline-block;
      width: 0;
      height: 2px;
      background-color: var(--color-primary);
      transition: all 0.3s ease-in-out;
      position: absolute;
      left: 0;
      bottom: -18.5px;
      display: none;
    }

    &.is-active {
      &::after {
        display: block;
        width: 100%;
      }

      color: var(--color-primary_active);
    }
  }
}

// Login
.btn-login {
  cursor: pointer;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 1;
  text-decoration: none;
  color: var(--color-body);
  @include trans-link;

  .header__top-icon {
    margin-right: 8px;
  }

  &:hover {
    color: var(--color-primary_active);
  }
}

// Logo {
.logo {
  width: 320px;
  display: flex;
  align-items: center;

  &__link {
    display: block;
    flex-shrink: 0;
  }

  &__img {
    display: block;
    flex-shrink: 0;
    width: 109px;
    height: 48px;
    margin-right: 16px;
  }

  .description {
    display: inline-block;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
  }
}

// btn-catalog
.btn-catalog {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

// Search
.form-search {
  &__inner {
    position: relative;
  }

  .input-search {
    width: 100%;
    border-radius: 6px;
  }

  &__btn {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    line-height: 1;
    width: 48px;
    height: 48px;
    color: var(--color-text-gray);
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.14);
    border-radius: 0px 6px 6px 0px;
    @include trans-btn;
    position: absolute;
    top: 0;
    right: 0;

    &:hover {
      background: var(--color-primary);
      border: 1px solid transparent;
      color: #fff;
    }
  }
}

// Cart Btn
.btn-cart {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color-primary);
  border-radius: 4px;
  border: 1px solid var(--color-line-gray);
  position: relative;
  line-height: 1;
  .bx {
    top: 0;
  }

  &__number {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    color: #fff;
    background-color: var(--color-title);
    border-radius: 50%;
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    position: absolute;
    top: -6px;
    right: -10px;
  }

  &:hover {
    border: 1px solid transparent;
    background: var(--color-primary);
    color: #fff;
  }

  &.is-active .btn-cart__number {
    background-color: var(--color-accent-red);
    border: 1px solid #fff;
    animation: anim-cart 1s infinite;
  }
}

// Animation
.cart-number-anim {
  animation: anim-cart 1s infinite;
}

@keyframes anim-cart {
  0% {
    box-shadow: 0px 0px 0px 3px rgba(240, 61, 34, 0);
  }

  50% {
    box-shadow: 0px 0px 0px 3px rgba(240, 61, 34, 0.2);
  }

  100% {
    box-shadow: 0px 0px 0px 3px rgba(240, 61, 34, 0);
  }
}

// Phone + Callback
.phone {
  text-align: right;

  &__number {
    font-weight: 700;
    font-size: 16px;
    line-height: 1;
    color: var(--color-title);
    text-decoration: none;

    &:hover {
      color: var(--color-primary_active);
      text-decoration: none;
    }
  }

  &__callback {
    font-size: 14px;
    line-height: 1;
    display: flex;
    align-items: center;
    text-decoration: none;

    .bx {
      font-size: 16px;
    }

    &:hover {
      color: var(--color-primary_active);
      text-decoration: none;
    }
  }
}

// Chat
.dropdown-chat {
  position: relative;

  &:hover .dropdown-chat__list {
    opacity: 1;
    pointer-events: visible;
  }

  .btn-chat {
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: url("data:image/svg+xml;base64,")
      no-repeat center right 8px;
    background-size: 20px 20px;
    border: 1px solid var(--color-line-gray);
    font-size: 14px;
    color: var(--color-body);
    padding: 0 20px;
    padding-right: 24px;
    position: relative;

    &__icon {
      color: var(--color-text-gray);
    }

    &__text {
      padding-right: 8px;
    }

    &:hover {
      color: inherit;
      background: transparent
        url("data:image/svg+xml;base64,")
        no-repeat center right 8px;
      border: 1px solid var(--color-primary);
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 140px;
    background: #ffffff;
    border: 1px solid #eeeeee;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.08);
    border-radius: 6px;
    padding: 10px;
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 10;
    transition: all 0.3s ease-in-out;
    opacity: 0;
    pointer-events: none;

    li {
      &:not(:last-child) {
        margin-bottom: 8px;
      }

      a {
        display: flex;
        align-items: center;
        text-decoration: none;

        &:hover {
          color: var(--color-primary_active);
        }
      }

      .bxl-whatsapp {
        color: #25d366;
      }

      .bxl-telegram {
        color: #2ba4e0;
      }

      .bx-mail-send {
        color: var(--color-primary);
      }
    }
  }
}

// Menu Category
.menu-category {
  width: 100%;
  background-color: var(--color-bg_dark-1);
  padding: 12px;
  display: flex;
  justify-content: center;

  .menu-top__link {
    color: #fff;
    opacity: 0.9;

    &:hover {
      color: var(--color-primary_hover);
    }

    &::after {
      display: none;
    }
  }
}

// burger
.burger {
  @include burger;
  margin-right: 12px;
  display: none;
}

@media (max-width: 1280px) {
  .burger {
    display: block;
  }

  .menu-category {
    display: none;
  }

  .dropdown-chat .btn-chat {
    background: #fff;
    padding: 0;
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;

    .bx__left {
      margin-right: 0;
    }

    &:hover {
      background: #fff;
    }
  }

  .dropdown-chat .btn-chat__text {
    display: none;
  }
}

@media (max-width: 1080px) {
  .burger {
    position: absolute;
    top: 61px;
    left: 16px;
  }

  .header {
    &__address {
      width: inherit;
    }

    .menu-top,
    .phone,
    .btn-catalog {
      display: none;
    }

    .header__search .form-search {
      margin-left: 0;
    }

    .logo {
      margin-left: 48px;
    }

    .dropdown-chat .btn-chat__text {
      display: none;
    }

    .btn-chat {
      width: 48px;
      height: 48px;
      padding: 0;
      background: none;
      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        background: none;
      }

      &__icon {
        margin-right: 0;
      }
    }
  }
}

// Медиа запросы
@media (max-width: 1024px) {
  .header .burger {
    left: 24px;
  }
}

@media (max-width: 992px) {
  .header .burger {
    left: 24px;
  }
}

@media (max-width: 800px) {
  .header__search-container {
    flex-wrap: wrap;
  }

  .header__search--box-1 {
    width: 100%;
    order: 3;
    padding: 16px 0px 0 0px;
  }
}

@media (max-width: 767px) {
  .header .burger {
    left: 16px;
  }
}

@media (max-width: 600px) {
  .header .logo {
    width: 250px;
  }

  .header .logo .description .h-mob {
    display: none;
  }

  .header .phone {
    display: none;
  }
}

@media (max-width: 480px) {
  .burger {
    width: 24px;
    top: 20px;
  }

  .header__top-container {
    display: flex;
    justify-content: flex-end;
  }

  .header__search .btn-catalog {
    display: none;
  }

  .header__search .form-search {
    margin-left: 0;
  }

  .header .logo {
    margin-left: 40px;
  }

  .header .logo__img {
    width: auto;
    height: 32px;
    margin-right: 8px;
  }

  .header__top {
    background: transparent;
    width: inherit;
    padding: 0;
    display: block;
    border-bottom: none;
    position: absolute;
    top: 24px;
    right: 0;
  }

  .header__address,
  .header .btn-chat {
    display: none;
  }

  .header__search {
    padding: 16px 0;
  }
}

@media (max-width: 375px) {
  .btn-login {
    width: 44px;
    height: 44px;
    border: 1px solid var(--color-line-gray);
    border-radius: 4px;
    justify-content: center;
    position: relative;
    top: -16px;
  }

  .btn-login__text {
    display: none;
  }

  .btn-login .header__top-icon {
    margin-right: 0;
  }

  .header__search--box-1 {
    padding-top: 12px;
  }

  .header .form-search .input-search {
    height: 44px;
  }

  .header__search .form-search {
    margin-right: 12px;
  }

  .header .form-search__btn {
    width: 44px;
    height: 44px;
  }

  .header .btn-cart {
    width: 44px;
    height: 44px;
  }

  .header .logo .description {
    font-size: 12px;
  }
}
