.dropdown {
  display: block;
  width: 100%;
  max-width: 248px;
  border-radius: 6px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  z-index: 20;
  position: relative;
  transition: all 0.3s ease-in-out;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
  }

  &__search {
    width: 100%;
    background-color: #fff;
    padding: 10px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    .input-search {
      width: 100%;
      color: #333;
    }
  }
  &__list {
    width: 100%;
    height: auto;
    overflow: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    position: relative;

    &--scroll {
      height: 260px;
      top: 72px;
    }

    // стилизация скрола
    &::-webkit-scrollbar {
      width: 4px; /* ширина scrollbar */
    }
    &::-webkit-scrollbar-track {
      background: rgba(0, 0, 0, 0.1); /* цвет дорожки */
    }
    &::-webkit-scrollbar-thumb {
      background: rgba(0, 0, 0, 0.1); /* цвет плашки */
      border-radius: 100px; /* закругления плашки */
      border: 2px solid var(--color-accent-yellow); /* padding вокруг плашки */
    }
  }
  &__item {
    width: 100%;
    background: transparent;
    font-size: 16px;
    line-height: 1;
    padding: 12px 16px;
    color: #333;
    cursor: pointer;
    @include trans-link;
    &:hover {
      color: var(--color-body);
      background: rgba(#000, 0.06);
    }

    &.is-active {
      color: #fff;
      background: var(--color-primary)
        url("data:image/svg+xml,%3Csvg%20width%3D%2218%22%20height%3D%2218%22%20viewBox%3D%220%200%2018%2018%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cg%20id%3D%22380a761f53a429f2053c4510b8e4c93f%22%3E%0A%20%20%20%20%3Cpath%20id%3D%22f7b3e2b6c31b8d0ce84ef61163760704%22%20d%3D%22M9%201.5C4.8645%201.5%201.5%204.8645%201.5%209C1.5%2013.1355%204.8645%2016.5%209%2016.5C13.1355%2016.5%2016.5%2013.1355%2016.5%209C16.5%204.8645%2013.1355%201.5%209%201.5ZM7.50075%2012.3098L4.716%209.531L5.775%208.469L7.49925%2010.1902L11.8448%205.84475L12.9053%206.90525L7.50075%2012.3098Z%22%20fill%3D%22white%22%3E%3C%2Fpath%3E%0A%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E")
        no-repeat;
      background-position: right 10px center;
      font-weight: 500;
    }
  }
}

.dropdown-links {
  .dropdown__item {
    padding: 0;
    margin: 0;
    &:hover {
      background: none;
    }
  }
  .dropdown__link {
    display: block;
    width: 100%;
    background: transparent;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
    text-decoration: none;
    text-align: left;
    padding: 12px 16px;
    color: #333;
    cursor: pointer;
    @include trans-link;
    &:hover {
      color: #fff;
      background: var(--color-primary);
    }
  }
}

@media (max-width: 480px) {
  .dropdown__item {
    font-size: 14px;
    line-height: 1.4;
    padding: 12px 16px;
  }
}
