.podbor {
  color: #fff;
  position: relative;
  background: rgba(33, 33, 33, 0.4);
  backdrop-filter: blur(8px);
  border-radius: 0px 8px 8px 8px;
  padding: 24px;
  padding-top: 0;
  position: relative;

  &__title {
    color: #fff;
    margin-bottom: 0;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.2;
    margin-bottom: 16px;
  }

  .tips-moto {
    display: flex;
    align-items: center;
    position: absolute;
    top: -48px;
    left: 0;
  }

  &__selects {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    .selects-group {
      display: flex;
      flex-wrap: wrap;
      flex-grow: 1;

      .select-custom {
        flex: 1;

        &:not(:last-child) {
          margin-right: 16px;
        }
      }

      .select-custom__item {
        border: 1px solid transparent;

        &:hover {
          border: 1px solid var(--color-primary);
        }
      }
    }
  }

  // стили подбора на других страницах
  &--pages {
    background: url("../img/podbor-bg.jpg") no-repeat center;
    background-size: cover;
    border-radius: 0;
    padding: 24px;
    padding-top: 16px;
    position: relative;

    &::before {
      display: block;
      content: "";
      width: 100%;
      background: #292929;
      opacity: 0.9;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: -1;
    }

    .container {
      position: relative;
    }

    .options-podbor {
      display: none;

      &.is-active {
        display: block;
      }
    }

    .options-podbor__group {
      padding: 0;
      margin-bottom: 16px;
    }

    .tips-moto {
      position: inherit;
      top: inherit;
      left: inherit;
      margin-bottom: 0;

      &__item {
        width: 100%;
        background-color: transparent;
        border: 1px solid rgba(255, 255, 255, 0.24);
        backdrop-filter: none;
        padding: 0;
        border-radius: 6px;
        padding: 0 24px;

        &:not(:last-child) {
          // border: none;
          margin-right: 16px;
        }

        &:hover {
          background: transparent;
          border: 1px solid var(--color-primary);
        }

        &.is-active:hover {
          background: #fff;
          color: #333;
          font-weight: 500;
        }
      }
    }

    .podbor__btn--clear {
      top: 0;
      right: 16px;
      display: none;

      &.is-active {
        display: block;
      }
    }

    .podbor__selects {
      display: none;

      &.is-active {
        display: flex;
        margin-top: 16px;
      }
    }
  }

  &__btn--success {
    margin-left: 16px;
  }

  &__btn--clear {
    display: inline-block;
    cursor: pointer;
    font-size: 14px;
    line-height: 1.2;
    position: absolute;
    top: 16px;
    right: 24px;
    color: rgba(255, 255, 255, 0.6);
    @include trans-link;
    display: none;

    &:hover {
      color: #fff;
    }

    &.is-active {
      display: inline-block;
    }
  }
}

// Тип транспорта
.tips-moto {
  &__item {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 48px;
    background: rgba(33, 33, 33, 0.6);
    backdrop-filter: blur(8px);
    color: rgba(#fff, 0.8);
    font-weight: 400;
    font-size: 16px;
    border-radius: 6px 6px 0px 0px;
    padding: 0 24px;
    @include trans-btn;

    &:not(:last-child) {
      border-right: 1px solid rgba(255, 255, 255, 0.2);
    }

    &:hover {
      background: #333;
    }

    &.is-active {
      background: #fff;
      color: #333;
      font-weight: 500;
    }
  }

  &__icon {
    display: inline-block;
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-size: 32px 32px;
    vertical-align: middle;
    flex-shrink: 0;
    margin-right: 12px;
  }

  &__name {
    font-size: 16px;
    line-height: 1;
  }
}

// иконки мото
.ic-motobike {
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  background: url("../img/icons/ic-motobike.png") no-repeat center;
  background-repeat: no-repeat;
  background-size: cover;
}

.ic-atv {
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  background: url("../img/icons/ic-atv.png") no-repeat center;
  background-repeat: no-repeat;
  background-size: cover;
}

.ic-scooter {
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  background: url("../img/icons/ic-scooter.png") no-repeat center;
  background-repeat: no-repeat;
  background-size: cover;
}

.ic-snowmobile {
  width: 32px;
  height: 32px;
  flex-shrink: 0;
  background: url("../img/icons/ic-snowmobile.png") no-repeat center;
  background-repeat: no-repeat;
  background-size: cover;
}

// Выбранные параметры
.options-podbor {
  &__group {
    display: flex;
    align-items: center;
    padding: 16px 0 16px 0;
  }

  &__label {
    font-weight: 500;
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__item {
    display: inline-block;
    color: var(--color-primary_hover);
    position: relative;
    padding: 0 8px;
    display: none;

    &.is-active {
      display: block;
    }

    &::after {
      content: "/";
      color: #fff;
      opacity: 0.2;
      position: relative;
      left: 8px;
    }

    &:last-child {
      margin-right: 8px;
    }
  }
}

@media (max-width: 1024px) {
  .podbor--pages {
    .container {
      padding: 0;
    }
  }
}

@media (max-width: 800px) {
  .podbor {
    .tips-moto__item {
      justify-content: center;
    }

    .tips-moto__icon {
      margin-right: 0;
    }

    .tips-moto__name {
      display: none;
    }
  }
}

@media (max-width: 767px) {
  .podbor {
    &__selects {
      flex-direction: column;
      align-items: center;
    }

    .selects-group {
      width: 100%;
      margin-bottom: 16px;
    }

    &__btn {
      &--success {
        width: 100%;
        margin-left: 0;
      }
    }
  }
}

@media (max-width: 600px) {
  .podbor {
    .selects-group {
      flex-direction: column;
    }

    .select-custom {
      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }

    &__btn--clear {
      position: inherit;
      top: inherit;
      right: inherit;
      margin-top: 16px;
      width: 100%;
      height: 48px;
      line-height: 46px;
      border: 1px solid rgba(255, 255, 255, 0.4);
      border-radius: 6px;
      font-weight: 500;
      font-size: 16px;
    }
  }
}

@media (max-width: 480px) {
  .podbor {
    padding: 16px;

    .tips-moto__item {
      width: 100%;
      padding: 0 8px;
      text-align: center;

      &:not(:last-child) {
        margin-right: 12px;
      }
    }

    &--home {
      padding-top: 0;

      .tips-moto {
        width: 100%;
      }

      .tips-moto__item {
        width: 100%;

        &:not(:last-child) {
          margin-right: 0px;
        }
      }
    }

    .options-podbor__group {
      flex-wrap: wrap;
    }

    .options-podbor__item {
      font-size: 14px;
      line-height: 1.2;
      padding: 0;
      margin-right: 16px;
    }
  }
}
