.feedback {
  &__block {
    width: 100%;
    // background: url("../img/feedback-bg.jpg") no-repeat top/cover;
    border-radius: 8px;
    padding: 64px;
    text-align: center;
    color: #fff;
    position: relative;
    overflow: hidden;
    z-index: 1;

    // &::before {
    //   display: block;
    //   content: "";
    //   width: 100%;
    //   height: 100%;
    //   background: #333;
    //   opacity: 0.9;
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   right: 0;
    //   bottom: 0;
    //   z-index: -1;
    // }
  }

  &__head {
    margin-bottom: 32px;
  }

  &__title {
    color: #fff;
    font-size: 36px;
    line-height: 1.2;
    font-weight: 400;
    margin-bottom: 16px;
  }

  &__descr {
    font-size: 20px;
    line-height: 1.4;
  }

  &__phone {
    text-decoration: none;
    display: inline-block;
    color: var(--color-primary_hover);
    font-weight: 700;
  }

  .btn {
    &:not(:last-child) {
      margin-right: 16px;
    }
  }
}

.btn-telegram {
  background: #2ba4e0;
  &:hover {
    background: #2493cb;
  }
}

@media (max-width: 1024px) {
  .feedback__block {
    padding: 48px;
  }
}

@media (max-width: 800px) {
  .feedback__title {
    font-size: 32px;
  }
}

@media (max-width: 600px) {
  .feedback__block {
    padding: 32px;
  }
}

@media (max-width: 576px) {
  .feedback__title {
    font-size: 28px;
  }

  .feedback__descr {
    font-size: 18px;
  }

  .feedback__buttons {
    display: flex;
    justify-content: space-between;

    .btn {
      width: 100%;
    }
  }
  .feedback__buttons .btn__text {
    display: none;
  }
}

@media (max-width: 480px) {
  .feedback__block {
    padding: 24px;
  }
}

@media (max-width: 390px) {
  .feedback__head {
    margin-bottom: 24px;
  }
  .feedback__title {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 8px;
  }

  .feedback__descr {
    font-size: 16px;
    line-height: 24px;
  }
}
