// кнопка наверх
.btn-top {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  display: inline-block;
  background: rgba(#000, 0.4);
  width: 40px;
  height: 40px;
  border-radius: 8px;
  position: fixed;
  bottom: 32px;
  right: 32px;
  transition: all 0.5s ease-in-out, visibility 0.5s;
  opacity: 0;
  visibility: hidden;
  z-index: 9999;
  @media (max-width: 480px) {
    bottom: 16px;
    right: 16px;
  }

  &:hover,
  &:active {
    background-color: var(--color-primary);
  }

  &::after {
    display: inline-block;
    content: "";
    width: 16px;
    height: 16px;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='20' height='20' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='m10.598 5.013 8.154 8.281c.33.33.33.865 0 1.195l-.499.5a.844.844 0 0 1-1.195 0L10 7.796 2.942 14.98a.844.844 0 0 1-1.195 0l-.5-.499a.844.844 0 0 1 0-1.195l8.155-8.281a.85.85 0 0 1 1.196.007Z' fill='%23fff'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &.is-active {
    opacity: 1;
    visibility: visible;

    &:hover {
      opacity: 1;
    }
  }
}
