.footer {
  color: rgba(255, 255, 255, 0.7);

  &__top {
    width: 100%;
    padding: 32px 0;
    background-color: var(--color-bg_dark-1);

    .container {
      display: flex;
      justify-content: space-between;
    }
  }

  &__col {
    padding: 0 12px;
  }

  &__subscribe {
    width: 30%;
  }

  &__list {
  }

  &__item {
    font-size: 15px;
    line-height: 1.4;
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__link {
    cursor: pointer;
    color: rgba(255, 255, 255, 0.7);
    text-decoration: none;

    &:hover {
      color: var(--color-primary);
      text-decoration: underline;
    }
  }

  .social {
    &__link {
      display: flex;
      align-items: center;

      &:hover {
        color: var(--color-primary);
        text-decoration: none;
      }

      .bx,
      .ic-dzen {
        display: inline-block;
        margin-right: 8px;
      }

      &:hover .ic-dzen {
        background: url("../img/icons/ic-dzen_h.png") no-repeat center/cover;
      }
    }
  }

  &__phone {
    font-size: 16px;
    line-height: 1;
    font-weight: 700;
    color: #fff;

    &:hover {
      text-decoration: none;
    }
  }

  &__callback {
    display: inline-block;
    border-bottom: 1px dashed rgba(255, 255, 255, 0.4);

    &:hover {
      text-decoration: none;
      border-bottom: 1px dashed var(--color-primary);
    }
  }

  &__bottom {
    width: 100%;
    padding: 12px 0;
    background: var(--color-bg_dark-2);

    p {
      margin-bottom: 0;
    }
  }

  &__bottom--grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__bottom--privacy {
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 1.4;
    margin-bottom: 4px;
    display: inline-block;
  }

  &__bottom--copyright {
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
    line-height: 1.4;
  }

  &__bottom--pay {
    width: 176px;
    display: inline-block;

    img {
      width: 100%;
      height: auto;
    }
  }
}

.form-subscribe {
  flex-basis: 2;

  &__title {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 12px !important;
  }

  &__email {
    width: 100%;
    position: relative;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.4);
    border-radius: 6px;
    color: #fff;
  }

  .input-box {
    width: 100%;
    height: 48px;
    position: relative;
    margin-bottom: 16px;

    .form-subscribe__btn {
      display: inline-block;
      background: none;
      border: none;
      outline: none;
      font-size: 0;
      width: 48px;
      height: 48px;
      background: var(--color-primary)
        url("data:image/svg+xml,%3Csvg%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cpath%20id%3D%2265e076a8adaa9cf2872fb37b57e3ee8c%22%20d%3D%22M4.41083%2010.4111L5.58917%2011.5895L11.1783%206.0003L5.58917%200.411133L4.41083%201.58947L7.98833%205.16697H0V6.83363H7.98833L4.41083%2010.4111Z%22%20fill%3D%22white%22%3E%3C%2Fpath%3E%0A%3C%2Fsvg%3E")
        no-repeat center;
      background-size: 14px 14px;
      border-radius: 0 6px 6px 0;
      position: absolute;
      top: 0;
      right: 0;
      &:hover {
        opacity: 0.8;
      }
    }
  }

  &__privacy {
    font-size: 13px;
    line-height: 1.4;
    opacity: 0.6;
    margin: 0;
  }
}

@media (max-width: 1024px) {
  .footer__top .container {
    flex-wrap: wrap;
  }

  .footer__subscribe {
    width: 100%;
  }
}

@media (max-width: 800px) {
  .footer .social {
    width: 30%;
    margin-top: 16px;
  }

  .footer__subscribe {
    width: 70%;
    margin-top: 16px;
  }

  .footer__bottom--grid {
    display: block;
    text-align: center;
  }

  .footer__bottom--pay {
    padding: 4px 0;
  }
}

@media (max-width: 600px) {
  .footer__top .container {
    display: block;
  }

  .footer__col {
    padding: 0;
    padding-top: 16px;

    &:not(:last-child) {
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      padding-bottom: 16px;
    }

    &:nth-child(2),
    &:nth-child(4) {
      border: none;
      padding-bottom: 12px;
    }

    &:nth-child(3) {
      padding-top: 0;
    }

    &:nth-child(4) {
      padding-bottom: 0;
    }

    &:nth-child(5) {
      padding-bottom: 24px;
    }
  }

  .footer .social,
  .footer__subscribe {
    width: 100%;
    padding-top: 0;
  }

  .footer .social .footer__list {
    display: flex;
    align-items: center;
  }

  .footer .social .footer__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 12px;
  }

  .footer .social__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    font-size: 0;
    .bx {
      display: inline-block;
      font-size: 20px;
      margin-right: 0;
    }

    .ic-dzen {
      margin-right: 0;
    }
  }
}

@media (max-width: 480px) {
  .footer__top {
    padding-top: 16px;
  }
}
