.news {
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    .more-link {
      font-size: 16px;
      font-weight: 400;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 24px;

    @media (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);

      .news__item:last-child {
        display: none;
      }
    }

    @media (max-width: 600px) {
      grid-template-columns: repeat(1, 1fr);

      .news__item:last-child {
        display: block;
      }
    }
  }

  &__item {
    cursor: pointer;
    text-decoration: none;
    transition: all 0.4s ease-in-out;

    &:hover .news__item--img::after {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
      background-position: center;
    }

    &:hover .news__item--title {
      color: var(--color-primary_active);
    }
  }

  &__item--img {
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    margin-bottom: 16px;
    transition: all 0.4s ease-in-out;
    img {
      width: 100%;
      height: auto;
    }

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4)
        url("data:image/svg+xml,%3Csvg%20width%3D%2248%22%20height%3D%2248%22%20viewBox%3D%220%200%2048%2048%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cg%20id%3D%227f8eb473b60a2a0c49fe586cf20ab3cc%22%3E%0A%20%20%20%20%3Crect%20id%3D%222b5d0bea4dc06ea7d902873c6aa083d2%22%20width%3D%2248%22%20height%3D%2248%22%20rx%3D%2224%22%20fill%3D%22%2300BA35%22%3E%3C%2Frect%3E%0A%20%20%20%20%3Cpath%20id%3D%2299ce9fb882869d47e2a132732c75a278%22%20d%3D%22M32.7923%2023.2385C31.0303%2020.2484%2027.7441%2018.25%2024%2018.25C20.2544%2018.25%2016.9689%2020.2498%2015.2077%2023.2385C15.0717%2023.4692%2015%2023.7322%2015%2024C15%2024.2678%2015.0717%2024.5308%2015.2077%2024.7616C16.9697%2027.7516%2020.2559%2029.75%2024%2029.75C27.7456%2029.75%2031.0311%2027.7502%2032.7923%2024.7615C32.9283%2024.5308%2033%2024.2678%2033%2024C33%2023.7322%2032.9283%2023.4692%2032.7923%2023.2385V23.2385ZM24%2028.25C21.6512%2028.25%2019.75%2026.3491%2019.75%2024C19.75%2021.6512%2021.6508%2019.75%2024%2019.75C26.3488%2019.75%2028.25%2021.6508%2028.25%2024C28.25%2026.3488%2026.3492%2028.25%2024%2028.25ZM27.25%2024C27.25%2025.7949%2025.7949%2027.25%2024%2027.25C22.2051%2027.25%2020.75%2025.7949%2020.75%2024C20.75%2023.4466%2020.8885%2022.9256%2021.1324%2022.4696L21.1323%2022.4706C21.1323%2023.2097%2021.7315%2023.8088%2022.4706%2023.8088C23.2097%2023.8088%2023.8088%2023.2096%2023.8088%2022.4706C23.8088%2021.7315%2023.2097%2021.1323%2022.4706%2021.1323L22.4696%2021.1324C22.9257%2020.8885%2023.4466%2020.75%2024%2020.75C25.7949%2020.75%2027.25%2022.2051%2027.25%2024Z%22%20fill%3D%22white%22%3E%3C%2Fpath%3E%0A%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E")
        no-repeat;
      background-position: center -200px;
      transition: all 0.4s ease-in-out;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }

    .badge {
      position: absolute;
      bottom: 0;
      right: 0;
      border-radius: 2px 0 0 0;
    }
  }

  &__item--date {
    display: flex;
    align-items: center;
    color: var(--color-text-gray);
    margin-bottom: 8px;
    .bx {
      margin-right: 8px;
      opacity: 0.6;
    }
  }

  &__item--title {
    display: block;
    height: 56px;
    overflow: hidden;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.4;
    transition: all 0.4s ease-in-out;

    &::after {
      content: "...";
    }
  }
}

@media (max-width: 1024px) {
  .news__item--title {
    font-size: 18px;
  }
}

@media (max-width: 576px) {
  .news__item--title {
    height: inherit;
  }

  .news__item--img::after {
    display: none;
  }
}
