:root {
  --container-width: 1216px;
  --container-padding: 24px;
  --font-default: "Roboto", -apple-system, BlinkMacSystemFont, Helvetica Neue,
    sans-serif;
  --fs-base: 16px;
  --fw-400: 400;
  --fw-500: 500;
  --fw-700: 700;
  --title-lh: 1.2;
  --body-lh: 1.5;
  --color-white: #fff;
  --color-black: #000;
  --color-body: #333;
  --color-title: #212121;
  --color-text-gray: #7b7b7b;
  --color-primary: #2dae2c;
  --color-primary_hover: #00ba35;
  --color-primary_active: #00992b;
  --color-accent-yellow: #f4af00;
  --color-accent-purple: #8954ec;
  --color-accent-blue: #2892d9;
  --color-accent-red: #f03d22;
  --color-success: #00ba35;
  --color-error: #d20b0b;
  --color-warning: #e69d0f;
  --color-bg_gray: #f0f1f5;
  --color-bg_dark-1: #212121;
  --color-bg_dark-2: #333333;
  --color-line-gray: rgba(0, 0, 0, 0.15);
  --color-border-dark: rgba(0, 0, 0, 0.15);
}

html {
  box-sizing: border-box;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

body {
  background-color: #f5f5f5;
  min-width: 320px;
  margin: 0;
  font-family: var(--font-default);
  font-style: normal;
  font-size: var(--fs-base);
  font-weight: var(--fw-400);
  line-height: var(--body-lh);
  font-display: swap;
  overflow-x: hidden;
  color: var(--color-body);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.dis-scroll {
  overflow: hidden;
}

.scroll-line::-webkit-scrollbar {
  width: 4px; /* ширина scrollbar */
}

.scroll-line::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1); /* цвет дорожки */
}

.scroll-line::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1); /* цвет плашки */
  border-radius: 100px; /* закругления плашки */
  border: 2px solid var(--color-primary_active); /* padding вокруг плашки */
}

.page__wrapper {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-width: 1920px;
  margin: 0 auto;
}

.page__main {
  flex-grow: 1;
}

.container {
  max-width: var(--container-width);
