/* Стиль списка */
.breadcrumb__list {
  list-style: none;
  padding: 24px 0;
}
.breadcrumb__item {
  display: inline;
  font-size: 15px;
}
.breadcrumb__link {
  color: var(--color-text-gray);
  font-size: 15px;
  line-height: 1.4;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}
.breadcrumb__link:hover {
  color: var(--color-primary_active);
  text-decoration: underline;
  opacity: 1;
}
.breadcrumb__link.active {
  color: var(--color-body);
  text-decoration: none !important;
}
.breadcrumb__item + li::before {
  content: "›";
  color: rgba(0, 0, 0, 0.6);
  font-size: 18px;
  display: inline-block;
  padding: 0 4px;
}

@media (max-width: 800px) {
  .breadcrumb__list {
    padding: 16px 0;
    padding-bottom: 24px;
  }
}
@media (max-width: 480px) {
  .breadcrumb__list {
    padding: 16px 0;
  }
  .breadcrumb__item,
.breadcrumb__link {
    font-size: 14px;
  }
}
.btn-top {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  display: inline-block;
  background: rgba(0, 0, 0, 0.4);
  width: 40px;
  height: 40px;
  border-radius: 8px;
  position: fixed;
  bottom: 32px;
  right: 32px;
  transition: all 0.5s ease-in-out, visibility 0.5s;
  opacity: 0;
  visibility: hidden;
  z-index: 9999;
}
@media (max-width: 480px) {
  .btn-top {
    bottom: 16px;
    right: 16px;
  }
}
.btn-top:hover, .btn-top:active {
  background-color: var(--color-primary);
}
.btn-top::after {
  display: inline-block;
  content: "";
