// таблица с ценами
.price-table {
  border: 1px solid var(--color-border-dark);
  border-radius: 4px;
  // overflow: hidden;

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    cursor: pointer;
    width: 100%;
    padding: 24px;
    background-color: #fff;
    transition: all 0.3s ease-in-out;

    &:not(:last-child) {
      border-bottom: 1px solid var(--color-border-dark);
    }

    &:hover {
      transform: translateY(-10px);
      border-radius: 8px;
      box-shadow: 0px 8px 32px rgba(45, 49, 65, 0.24);
    }

    &.in-cart {
      background: #f5fcf7;
    }

    .tovar__btn {
      cursor: pointer;

      &.in-card {
        background: var(--color-primary);
        color: #fff;
      }
    }
  }
  &__prices {
    width: 30%;
    display: flex;
    align-items: center;

    .price--rozn {
      margin-right: 16px;
    }

    .price--club {
      margin-left: 16px;
    }
  }

  &__footer-info {
    text-align: center;
    padding: 12px 16px;
    background: #f0f1f5;

    p {
      font-size: 14px;
      line-height: 1.4;
      margin: 0;
    }
  }

  .tovar-sale {
    flex-shrink: 0;
  }
}

// цена
.price {
  display: flex;
  flex-direction: column;

  &__sum {
    display: block;
    font-size: 20px;
    font-weight: 500;
    color: var(--color-title);
  }

  &__label {
    font-size: 14px;
    line-height: 1;
    color: var(--color-text-gray);
  }

  &__info {
    display: block;
    width: 200px;
    border: 1px solid var(--color-border-dark);
    border-radius: 6px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.2;
  }

  &__info--num {
    color: var(--color-primary_active);
  }
}

.price--club {
  .price__sum {
    color: var(--color-primary_active);
  }
}

.price-row-club .price--rozn {
  .price__sum {
    opacity: 0.6;
  }
}

@media (max-width: 1024px) {
  .price-table__row {
    padding: 16px;
  }

  .price-table__row .price-table__prices {
    width: 35%;
  }
}

@media (max-width: 800px) {
  .price-table__row {
    position: relative;
    padding-top: 40px;

    &:hover {
      transform: none;
      box-shadow: none;
      border-radius: none;
    }

    .price__info {
      display: inline-block;
      width: 100%;
      background: transparent;
      border-radius: none;
      border: none;
      position: absolute;
      top: 0;
      left: 0;
      padding: 0;

      &--label {
        background: #f0f1f5;
        border-radius: 0px 0px 4px 0px;
        padding: 6px 12px;
        display: inline-block;
      }

      &--label br {
        display: none;
      }
    }

    // &.in-cart .price__info--label {
    //   background-color: #fff;
    // }
  }

  .price-table__row .price-table__prices {
    width: 40%;
  }

  .price__sum {
    font-size: 18px;
  }
}

@media (max-width: 767px) {
  .price-table__row .price__info {
    width: 100%;
  }

  .price-table__row .price__info--label {
    width: 100%;
    text-align: center;
    border-radius: 0;
  }

  .price-table__row .price-table__prices {
    width: auto;
    .price {
      margin-right: 0;
      text-align: right;
      &--rozn {
        text-align: left;
      }
    }
  }

  .price-row-club .price-table__prices {
    width: 100%;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  .price-table__row .tovar__btn {
    width: 100%;
    margin-top: 16px;
  }
}
