// рейтинг выбор
.rating-area {
  display: flex;
  align-items: center;
  overflow: hidden;
  margin: 0 auto;
}

.rating-area:not(:checked) > input {
  display: none;
}

.rating-area:not(:checked) > label {
  float: right;
  width: 42px;
  padding: 0;
  cursor: pointer;
  font-size: 32px;
  line-height: 32px;
  color: rgba(#000, 0.2);
}

.rating-area:not(:checked) > label:before {
  content: "☆";
}

.rating-area > input:checked ~ label {
  color: var(--color-accent-yellow);
}

.rating-area > input:checked ~ label::before {
  content: "★";
}

.rating-area:not(:checked) > label:hover,
.rating-area:not(:checked) > label:hover ~ label {
  color: var(--color-accent-yellow);
}

.rating-area > input:checked + label:hover,
.rating-area > input:checked + label:hover ~ label,
.rating-area > input:checked ~ label:hover,
.rating-area > input:checked ~ label:hover ~ label,
.rating-area > label:hover ~ input:checked ~ label {
  color: var(--color-accent-yellow);
  text-shadow: 1px 1px var(--color-accent-yellow) enrod;
}

.rate-area > label:active {
  position: relative;
}

// Результат
.rating-result {
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.rating-result span {
  padding: 0;
  font-size: 24px;
  margin: 0 4px;
  line-height: 1;
  color: rgba(#000, 0.3);
  border-radius: 2px;
}

.rating-result > span:before {
  content: "☆";
}

.rating-result > span.is-active {
  content: "★";
  color: var(--color-accent-yellow);
}

.rating-result > span.is-active::before {
  content: "★";
}

// Мини рейтинг
.rating-mini {
  display: flex;
  align-items: center;
  font-size: 0;
}

.rating-mini span {
  padding: 0;
  font-size: 18px;
  line-height: 1;
  color: lightgrey;
}

.rating-mini > span:before {
  content: "☆";
}

.rating-mini > span.is-active {
  color: var(--color-accent-yellow);
}

.rating-mini > span.is-active::before {
  content: "★";
}
