// Кнопка или ссылка-кнопка с текстом
.btn {
  display: inline-block;
  height: 48px;
  line-height: 45px;
  color: #fff;
  background: var(--color-primary);
  border: 1.4px solid transparent;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  font-size: 16px;
  font-weight: var(--fw-500);
  border-radius: 6px;
  padding: 0 24px;
  transition: all 0.3s ease-in-out;
  position: relative;

  &:hover {
    background: var(--color-primary_hover);
  }

  &:active {
    background: var(--color-primary_active);
  }

  // Style
  &__outline {
    background: transparent;
    color: var(--color-primary_active);
    border: 1.4px solid var(--color-primary);
    &:hover {
      background: var(--color-primary);
      color: var(--color-white);
      border: 1.4px solid var(--color-primary);
    }
  }

  // Size
  &--lg {
    height: 56px;
    line-height: 52px;
    padding: 0 32px;
    border-radius: 8px;
  }

  &--md {
    height: 48px;
    line-height: 45px;
    padding: 0 24px;
    border-radius: 6px;
  }

  &--sm {
    height: 40px;
    line-height: 37px;
    font-size: 14px;
    padding: 0 20px;
    border-radius: 4px;
  }
}

// Кнопка в виде иконки
.btn-icon {
  display: inline-block;
  width: 48px;
  height: 48px;
  line-height: 48px;
  color: #fff;
  background: var(--color-primary);
  border: 1.4px solid transparent;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  font-size: 24px;
  font-weight: var(--fw-500);
  border-radius: 6px;
  padding: 0;
  transition: all 0.3s ease-in-out;
  position: relative;

  &:hover {
    background: var(--color-primary_hover);
  }

  &:active {
    background: var(--color-primary_active);
    top: 1px;
  }

  &--outline {
    background: transparent;
    color: var(--color-primary);
    border: 1.4px solid var(--color-primary);
    &:hover {
      background: var(--color-primary);
      color: var(--color-white);
      border: 1.4px solid var(--color-primary);
    }
  }
  &--lg {
    width: 56px;
    height: 56px;
    line-height: 56px;
  }
  &--md {
    width: 48px;
    height: 48px;
    line-height: 48px;
  }
  &--sm {
    width: 40px;
    height: 40px;
    line-height: 40px;
  }

  .icon-btn {
    top: -3px;
  }
}

// Иконка в кнопке
.icon-btn {
  display: inline-block;
  font-size: 24px;
  line-height: 1;
  vertical-align: middle;
  position: relative;
  top: -1px;
}

@media (max-width: 480px) {
  .btn {
    width: 100%;
  }
}
