// счетчик товара
.t-counter {
  display: flex;
  align-items: center;
  height: 48px;

  // кнопки плюс и минус
  &__btn {
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    display: inline-block;
    width: 48px;
    height: 100%;
    border: 1px solid var(--color-border-dark);
    border-radius: 6px;
    position: relative;
    transition: all 0.4s ease-in-out;

    &::before,
    &::after {
      display: inline-block;
      font-size: 16px;
      font-weight: 500;
      color: var(--color-body);
      transition: all 0.4s ease-in-out;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &--minus {
      border-radius: 6px 0px 0px 6px;

      &::before {
        content: "−";
      }
    }

    &--plus {
      border-radius: 0px 6px 6px 0px;

      &::after {
        content: "+";
        font-size: 18px;
      }
    }

    &:hover {
      &::before,
      &::after {
        color: var(--color-primary);
      }
    }
  }

  &__input {
    background: none;
    border: none;
    outline: none;
    width: 48px;
    height: 100%;
    border-top: 1px solid var(--color-border-dark);
    border-bottom: 1px solid var(--color-border-dark);
    border-radius: 0;
    font-size: 16px;
    font-weight: 500;
    color: #000;
    text-align: center;
    margin: 0;
    padding: 4px;
  }
}

@media (max-width: 800px) {
  .t-counter {
    height: 40px;

    &__btn {
      border-radius: 4px;

      &--minus {
        border-radius: 4px 0px 0px 4px;
      }

      &--plus {
        border-radius: 0px 4px 4px 0px;
      }
    }

    &__input {
      width: 40px;
    }
  }
}
