.slider-arows {
  display: flex;
  align-items: center;
}

.slider-arow {
  flex-shrink: 0;
  display: block;
  background: none;
  border: none;
  outline: none;
  width: 40px;
  height: 40px;
  background-size: cover;
  border: 1px solid var(--color-line-gray);
  border-radius: 50%;
  transition: all 0.4s ease-in-out;
  margin: 0;

  &:hover {
    border: 1px solid var(--color-primary);
  }

  &:first-child {
    margin-right: 8px;
  }

  &--prev {
    background: url("data:image/svg+xml,%3Csvg%20width%3D%228%22%20height%3D%2212%22%20viewBox%3D%220%200%208%2012%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cpath%20id%3D%22882f4dade5433a7b982dc760cb4b85fd%22%20d%3D%22M6.29294%200.292969L0.585938%205.99997L6.29294%2011.707L7.70694%2010.293L3.41394%205.99997L7.70694%201.70697L6.29294%200.292969Z%22%20fill%3D%22%23333333%22%3E%3C%2Fpath%3E%0A%3C%2Fsvg%3E");
    background-repeat: no-repeat;
    background-position: center;
  }
  &--next {
    background: url("data:image/svg+xml,%3Csvg%20width%3D%228%22%20height%3D%2212%22%20viewBox%3D%220%200%208%2012%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cpath%20id%3D%226f12de6857f7482770ee27025cabb270%22%20d%3D%22M1.70697%2011.707L7.41397%205.99997L1.70697%200.292969L0.292969%201.70697L4.58597%205.99997L0.292969%2010.293L1.70697%2011.707Z%22%20fill%3D%22%23333333%22%3E%3C%2Fpath%3E%0A%3C%2Fsvg%3E");
    background-repeat: no-repeat;
    background-position: center;
  }

  @media (max-width: 480px) {
    width: 32px;
    height: 32px;
  }
}
