.select-custom {
  width: 100%;
  height: 48px;
  position: relative;
  &__item {
    cursor: pointer;
    width: 100%;
    height: 48px;
    line-height: 48px;
    background-color: #fff;
    border: 1px solid #d3d3d3;
    border-radius: 6px;
    color: rgba(#000, 0.6);
    font-size: 16px;
    padding: 0 16px;
    @include trans-link;
    &::after {
      content: "";
      width: 20px;
      height: 20px;
      background: url("data:image/svg+xml,%3Csvg%20width%3D%2210%22%20height%3D%227%22%20viewBox%3D%220%200%2010%207%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cpath%20id%3D%22a89b6183c22bb4feb62adda8b936239d%22%20d%3D%22M8.57747%200.744141L4.99997%204.32164L1.42247%200.744141L0.244141%201.92247L4.99997%206.67831L9.75581%201.92247L8.57747%200.744141Z%22%20fill%3D%22%23333333%22%3E%3C%2Fpath%3E%0A%3C%2Fsvg%3E")
        no-repeat center;
      position: absolute;
      @include trans-link;
      top: 14px;
      right: 12px;
      opacity: 0.6;
    }

    &:hover {
      border: 1px solid var(--color-primary);
    }
  }

  .dropdown {
    width: 100%;
    max-width: 100%;
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: 2px;
    display: none;
    z-index: 999;
    &.is-active {
      display: block;
    }
  }
}

.select-custom.is-active .dropdown {
  display: block;
}

.select-custom.is-active .select-custom__item {
  border: 1px solid var(--color-primary);
}

.select-custom.is-active .select-custom__item::after {
  transform: rotate(180deg);
  opacity: 1;
}
