// Типографика
// https://kalk.top/conver/px-rem

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--color-title);
  font-weight: var(--fw-500);
  line-height: var(--title-lh);
  margin-bottom: 0.5rem;
}

.h1,
h1 {
  @include font-size(2.5rem);
}

.h2,
h2 {
  @include font-size(2.25rem);
}

.h3,
h3 {
  @include font-size(2rem);
}

.h4,
h4 {
  @include font-size(1.75rem);
}

.h5,
h5 {
  @include font-size(1.5rem);
}

.h6,
h6 {
  @include font-size(1.125rem);
  font-weight: var(--fw-500);
}

// Heading Large
.h-display_1 .h-display_2 .h-display_3 .h-display_4 .h-display_5 .h-display_6 {
  color: var(--color-title);
  line-height: var(--title-lh);
  margin-bottom: 0.5rem;
}

.h-display_1 {
  @include font-size(3rem);
  font-weight: var(--fw-500);
}

.h-display_2 {
  @include font-size(2.5rem);
  font-weight: var(--fw-500);
}
.h-display_3 {
  @include font-size(2rem);
  font-weight: var(--fw-500);
}
.h-display_4 {
  @include font-size(1.75rem);
  font-weight: var(--fw-500);
}

.h-display_5 {
  @include font-size(1.5rem);
  font-weight: var(--fw-500);
}

.h-display_6 {
  @include font-size(1.25rem);
  font-weight: var(--fw-500);
}

@media (max-width: 360px) {
  h1,
  .h1,
  h2,
  .h2 {
    font-size: 1.5rem;
  }

  h4,
  .h4 {
    font-size: 1.25rem;
  }

  h5,
  .h5 {
    font-size: 1.125rem;
  }

  h6,
  .h6 {
    font-size: 1rem;
  }
}

.mark,
mark {
  padding: 0.2em;
  background-color: #fcf8e3;
}

.del {
  text-decoration: line-through;
}

.ins {
  text-decoration: underline;
}

.u {
  text-decoration: underline;
}

.small,
small {
  @include font-size(0.875rem);
}

b,
strong {
  font-weight: 700;
}

em {
  font-style: italic;
}

.blockquote {
  margin-bottom: 1rem;
  font-size: 1.25rem;
}

blockquote {
  margin: 0 0 1rem;
}

.text-start {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-end {
  text-align: right;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-lowercase {
  text-transform: lowercase;
}

p,
.p {
  margin-bottom: 1rem;
  &--lg {
    font-size: 1.125rem;
  }

  &--md {
    font-size: 1rem;
    line-height: 1.5;
  }

  &--sm {
    font-size: 0.875rem;
    line-height: 1.4;
  }

  &--xs {
    font-size: 0.75rem;
    line-height: 1.4;
  }
}

// List
.list {
  list-style-type: none;
  line-height: 1.5;
  margin-bottom: 0.5rem;
}

.list-dot {
  list-style-type: circle;
  margin-left: 16px;
}

.list-number {
  list-style-type: decimal;
  margin-left: 16px;
}

.list-row {
  display: flex;
  flex-wrap: wrap;
}

.list-tire {
  li::before {
    content: "—";
    position: relative;
    left: -5px;
  }
}

.link {
  color: var(--color-body);
  text-decoration: underline;
  transition: all 0.2s ease-in-out;
  &:hover {
    color: var(--color-primary);
    text-decoration: none;
  }
}

// Section
.section-head {
  margin-bottom: 4rem;
  @include media-max_1024 {
    margin-bottom: 3rem;
  }

  @include media-max_576 {
    margin-bottom: 2rem;
  }
}

// Section Padding
.p-section {
  padding: 6rem 0;
  @include media-max_1024 {
    padding: 4rem 0;
  }
  @include media-max_768 {
    padding: 3rem 0;
  }

  @include media-max_576 {
    padding: 2.5rem 0;
  }
  @include media-max_480 {
    padding: 2rem 0;
  }
}
