.brends {
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    .more-link {
      font-size: 16px;
      font-weight: 400;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 24px;

    @media (max-width: 800px) {
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 16px;
    }

    @media (max-width: 576px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 480px) {
      grid-gap: 8px;
    }

    .brend {
      max-width: 100%;

      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 0;
      }
    }
  }
}

// brend-item
.brend {
  display: inline-block;
  width: 100%;
  max-width: 180px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.14);
  border-radius: 8px;
  padding: 24px;
  text-decoration: none;
  text-align: center;
  transition: all 0.3s ease-in-out;

  &:not(:last-child) {
    margin-right: 16px;
    margin-bottom: 16px;
  }

  &:hover {
    background: #fff;
    border: 1px solid #f3f3f3;
    box-shadow: 0px 24px 32px rgba(45, 49, 65, 0.24);
    transform: translateY(-16px);

    @media (max-width: 1024px) {
      box-shadow: none;
      transform: none;
    }
  }

  &__logo {
    display: inline-block;
    width: 80px;
    height: 80px;
    margin-bottom: 16px;
    img {
      max-width: 100%;
      height: auto;
    }
  }

  &__name {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 500;
  }

  &__num {
    font-weight: 400;
    opacity: 0.8;
  }
}

@media (max-width: 480px) {
  .brend {
    padding: 16px;
  }

  .brend__logo {
    width: 64px;
    height: 64px;
  }
}

@media (max-width: 390px) {
  .brend {
    padding: 10px;
  }
  .brend__logo {
    width: 48px;
    height: 48px;
  }
}
