// вид сетки grid
.tovars-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 24px;

  @media (max-width: 600px) {
    grid-gap: 16px;
  }
}

// вертикальная карточка
.card-tovar {
  width: 100%;
  background-color: #fff;
  border: 1px solid var(--color-line-gray);
  padding: 24px;
  border-radius: 4px;
  position: relative;
  transition: all 0.4s ease-in-out;
  position: relative;

  &__badges {
    position: absolute;
    top: 16px;
    left: 16px;
    z-index: 10;
  }

  &:hover {
    background: #fff;
    border: 1px solid #f3f3f3;
    box-shadow: 0px 24px 32px rgba(45, 49, 65, 0.24);
    transform: translateY(-16px);

    @media (max-width: 480px) {
      transform: none;
    }
  }

  &:hover .card-tovar__cart-btn {
    background: var(--color-primary);
  }

  &__img {
    display: block;
    width: 180px;
    height: 180px;
    border-radius: 6px;
    margin: 0 auto;
    margin-bottom: 16px;
    text-align: center;
    position: relative;
    overflow: hidden;
    transition: all 0.4s ease-in-out;

    picture {
      display: block;
    }

    img {
      max-width: 100%;
      height: auto;
    }

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background: url("data:image/svg+xml,%3Csvg%20width%3D%2248%22%20height%3D%2248%22%20viewBox%3D%220%200%2048%2048%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cg%20id%3D%227f8eb473b60a2a0c49fe586cf20ab3cc%22%3E%0A%20%20%20%20%3Crect%20id%3D%222b5d0bea4dc06ea7d902873c6aa083d2%22%20width%3D%2248%22%20height%3D%2248%22%20rx%3D%2224%22%20fill%3D%22%2300BA35%22%3E%3C%2Frect%3E%0A%20%20%20%20%3Cpath%20id%3D%2299ce9fb882869d47e2a132732c75a278%22%20d%3D%22M32.7923%2023.2385C31.0303%2020.2484%2027.7441%2018.25%2024%2018.25C20.2544%2018.25%2016.9689%2020.2498%2015.2077%2023.2385C15.0717%2023.4692%2015%2023.7322%2015%2024C15%2024.2678%2015.0717%2024.5308%2015.2077%2024.7616C16.9697%2027.7516%2020.2559%2029.75%2024%2029.75C27.7456%2029.75%2031.0311%2027.7502%2032.7923%2024.7615C32.9283%2024.5308%2033%2024.2678%2033%2024C33%2023.7322%2032.9283%2023.4692%2032.7923%2023.2385V23.2385ZM24%2028.25C21.6512%2028.25%2019.75%2026.3491%2019.75%2024C19.75%2021.6512%2021.6508%2019.75%2024%2019.75C26.3488%2019.75%2028.25%2021.6508%2028.25%2024C28.25%2026.3488%2026.3492%2028.25%2024%2028.25ZM27.25%2024C27.25%2025.7949%2025.7949%2027.25%2024%2027.25C22.2051%2027.25%2020.75%2025.7949%2020.75%2024C20.75%2023.4466%2020.8885%2022.9256%2021.1324%2022.4696L21.1323%2022.4706C21.1323%2023.2097%2021.7315%2023.8088%2022.4706%2023.8088C23.2097%2023.8088%2023.8088%2023.2096%2023.8088%2022.4706C23.8088%2021.7315%2023.2097%2021.1323%2022.4706%2021.1323L22.4696%2021.1324C22.9257%2020.8885%2023.4466%2020.75%2024%2020.75C25.7949%2020.75%2027.25%2022.2051%2027.25%2024Z%22%20fill%3D%22white%22%3E%3C%2Fpath%3E%0A%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E")
        no-repeat;
      background-position: center -200px;
      transition: all 0.4s ease-in-out;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
    }

    &:hover::after {
      background-position: center;
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
    }
  }

  &__artikul {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--color-text-gray);
    margin-bottom: 4px;
  }

  &__name {
    overflow: hidden;
    display: block;
    max-height: 48px;
    color: var(--color-body);
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    text-decoration: none;
    margin-bottom: 12px;
    overflow: hidden;

    &:hover {
      color: var(--color-primary_active);
      text-decoration: underline;
    }

    &::after {
      content: "...";
    }
  }

  &__price {
    color: var(--color-title);
    font-weight: 500;
    font-size: 18px;
    line-height: 1;
    margin-bottom: 8px;
  }

  &__nalichie {
    font-size: 14px;
    line-height: 1;
    margin-bottom: 16px;
  }

  &__nalichie--sum {
    font-weight: 500;
    color: var(--color-primary_active);
  }

  &__buttons {
    display: flex;
  }

  &__more-btn {
    width: 100%;
    margin-right: 12px;
  }

  &__cart-btn {
    flex-shrink: 0;
    background: var(--color-bg_dark-2);

    .bx {
      top: -2px;
    }
  }

  &__badges {
    display: flex;
    flex-wrap: wrap;
  }

  &.in-cart {
    background: #f5fcf7;
    .card-tovar__cart-btn {
      background: var(--color-primary);
      font-size: 0;
      position: relative;
      .bx {
        display: none;
      }
      &::before {
        content: "";
        width: 20px;
        height: 20px;
        background: url("data:image/svg+xml,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20viewBox%3D%220%200%2020%2020%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%3Cg%20id%3D%22532af34f8ad5b4f31ef078ee17874ff0%22%3E%0A%20%20%20%20%3Cpath%20id%3D%220a3e1a7dc3d27a6aa3b96dc8de110495%22%20d%3D%22M10.0001%201.66667C5.40508%201.66667%201.66675%205.40501%201.66675%2010C1.66675%2014.595%205.40508%2018.3333%2010.0001%2018.3333C14.5951%2018.3333%2018.3334%2014.595%2018.3334%2010C18.3334%205.40501%2014.5951%201.66667%2010.0001%201.66667ZM8.33425%2013.6775L5.24008%2010.59L6.41675%209.41L8.33258%2011.3225L13.1609%206.49417L14.3392%207.6725L8.33425%2013.6775Z%22%20fill%3D%22white%22%3E%3C%2Fpath%3E%0A%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E")
          no-repeat center;
        background-size: 20px 20px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

// горизонтальная карточка
.card-tovar--horizontal {
  position: relative;
  border-radius: 8px;
  border: 1px solid transparent;

  .card-tovar__body {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
  }

  .card-tovar__img {
    width: 104px;
    height: 104px;
    flex-shrink: 0;
  }

  .card-tovar__img,
  .card-tovar__nalichie {
    margin-bottom: 0;
  }
}

// карточки списком
.tovars-list {
  display: block;
  border: 1px solid var(--color-border-dark);
  border-radius: 4px;
  overflow: hidden;
}

// карточка строка
.card-tovar--row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  border-color: transparent;
  border-radius: none !important;
  padding: 16px;

  &:hover {
    transform: none;
    border-radius: none;
    box-shadow: none;
  }

  &:not(:last-child) {
    border-bottom: 1px solid var(--color-border-dark);
  }

  .card-tovar__badges {
    top: 0;
    left: 0;
  }

  .badge {
    border-radius: 0 0 4px 0;
  }

  .card-tovar__body {
    width: 70%;
    display: grid;
    grid-template-columns: 80px 1fr;
    align-items: center;
  }

  .card-tovar__img {
    width: 64px;
    height: 64px;
    margin-bottom: 0;

    &::after {
      background-size: 32px 32px;
    }
  }

  .card-tovar__name {
    max-height: 64px;
    margin-bottom: 8px;
  }

  .card-tovar__price {
    font-size: 16px;
  }

  .card-tovar__nalichie {
    margin-bottom: 0;
  }
}

@media (max-width: 600px) {
  .card-tovar--row .card-tovar__body {
    width: 100%;
    margin-bottom: 16px;
  }

  .card-tovar__buttons {
    width: 100%;
  }
}

@media (max-width: 576px) {
  .card-tovar {
    padding: 16px;
  }

  .card-tovar__img {
    width: 120px;
    height: 120px;

    &::after {
      display: none;
    }
  }

  .card-tovar__name {
    height: inherit;
  }
}

@media (max-width: 480px) {
  .card-tovar__badges {
    top: 10px;
    left: 10px;
  }

  .card-tovar__body {
    display: flex;
    align-items: center;
  }

  .card-tovar__img {
    margin-right: 8px;
    margin-bottom: 0;
  }

  .card-tovar__name {
    line-height: 1.4;
  }

  .card-tovar__price {
    font-size: 16px;
  }
}
