.text-block {
  &:not(:last-child) {
    margin-bottom: 32px;
  }
  &__title {
    font-size: 28px;
    line-height: 1.2;
    font-weight: 400;
    margin-bottom: 16px;
    padding-left: 16px;
    border-left: 3px solid var(--color-primary);
  }
}

@media (max-width: 480px) {
  .text-block:not(:last-child) {
    margin-bottom: 24px;
  }
  .text-block__title {
    font-size: 24px;
  }
}

@media (max-width: 390px) {
  .text-block__title {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 12px;
  }
}

@media (max-width: 360px) {
  .text-block__title {
    font-size: 18px;
  }
}
