.pagination-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding-top: 32px;

  @media (max-width: 800px) {
    padding-top: 24px;
  }

  @media (max-width: 480px) {
    padding-top: 16px;
  }
}

// pagination
.pagination {
  &__list {
    display: flex;
    align-items: center;
    list-style: none;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  .--dots {
    @extend .pagination__link;
    cursor: default;
  }

  &__link {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #fff;
    border: 1px solid var(--color-border-dark);
    border-radius: 6px;

    &:not(:last-child) {
      margin-right: 8px;
    }

    color: var(--color-text-gray);
    font-size: 15px;
    font-weight: 500;
    line-height: 1;
    text-decoration: none;
    @include trans-link;

    &:hover {
      border: 1px solid var(--color-primary);
      color: var(--color-primary_active);
    }

    &.is-active {
      background: var(--color-primary);
      border: transparent;
      color: #fff;
    }
  }
}
