// Подкатегории
.categories {
  padding-top: 0;
  // position: relative;

  .container {
    position: relative;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    .more-link {
      font-size: 16px;
      font-weight: 400;
      line-height: 1.2;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 24px;

    @media (max-width: 600px) {
      grid-template-columns: repeat(1, 1fr);
    }

    @media (max-width: 480px) {
      grid-gap: 16px;
    }
  }
}

// Категория
.category {
  display: flex;
  background-color: #fff;
  border: 1px solid var(--color-line-gray);
  border-radius: 8px;
  padding: 24px;
  position: relative;

  &__img {
    flex-shrink: 0;
    width: 80px;
    height: 80px;
    border-radius: 8px;
    margin-right: 16px;
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__name {
    display: inline-block;
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    text-decoration: none;
    margin-bottom: 16px;
    transition: all 0.4s ease-in-out;
    &:hover {
      color: var(--color-primary_active);
    }
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
  }
}

// Подкатегории ссылки
.podcategory {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 16px;
  &__link {
    cursor: pointer;
    display: inline-block;
    text-decoration: none;
    font-size: 14px;
    line-height: 1.2;
    padding-bottom: 6px;

    &:not(:last-child) {
      margin-right: 8px;
    }
  }
  &__name {
    color: #4b4b4b;
    margin-bottom: 8px;
    &:hover {
      color: var(--color-primary_active);
      text-decoration: underline;
    }
  }
  &__num {
    opacity: 0.8;
    &:hover {
      color: inherit;
      text-decoration: inherit;
    }
  }

  &--mini {
    height: 120px;
    overflow: hidden;
  }
}

@media (max-width: 1024px) {
  .category {
    flex-direction: column;
  }

  .category__img {
    width: 104px;
    height: 104px;
    margin-right: 0;
    margin-bottom: 24px;
  }

  .podcategory--mini {
    height: 88px;
  }
}

@media (max-width: 600px) {
  .category__name {
    font-size: 18px;
    margin-bottom: 8px;
  }

  .podcategory__link {
    padding-bottom: 16px;
  }
  .podcategory--mini {
    height: 120px;
  }
}

@media (max-width: 480px) {
  .categories {
    padding-bottom: 96px;
  }
  .categories__header {
    display: block;

    .more-link {
      width: 93%;
      height: 48px;
      line-height: 46px;
      border: 1px solid var(--color-line-gray);
      border-radius: 6px;
      padding: 0 24px;
      position: absolute;
      bottom: -64px;
      left: 16px;
      text-align: center;
    }
  }

  .category {
    padding-bottom: 16px;
  }
  .category__img {
    width: 80px;
    height: 80px;
    margin-right: 0;
    margin-bottom: 16px;
  }
}
