.catalog {
  &__grid {
    display: grid;
    grid-template-columns: 3fr 9fr;
    grid-gap: 32px;
    position: relative;
  }

  .podcategories {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 32px;

    &__item {
      cursor: pointer;
      display: inline-block;
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      font-weight: 500;
      color: var(--color-body);
      background: #f9f9fb;
      border: 1px solid var(--color-border-dark);
      border-radius: 4px;
      padding: 0 16px;
      text-align: center;
      position: relative;
      @include trans-btn;

      &:not(:last-child) {
        margin-right: 16px;
        margin-bottom: 16px;
      }

      .num {
        font-weight: 400;
        opacity: 0.6;
      }

      .bx {
        margin-left: 4px;
        opacity: 0.8;
        @include trans-btn;
      }

      &.is-active {
        background: var(--color-primary);
        border: 1px solid transparent;
        color: #fff;
        .num {
          opacity: 1;
        }
        .bx {
          color: #fff;
          transform: rotate(180deg);
          opacity: 1;
        }
      }

      .dropdown {
        width: 220px;
        position: absolute;
        top: 100%;
        left: 0;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        @include trans-btn;

        &__link {
          font-size: 14px;
        }

        &.is-active {
          opacity: 1;
          visibility: visible;
          pointer-events: auto;
        }
      }
    }
  }

  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    .left-block,
    .right-block {
      display: flex;
      align-items: flex-end;
    }
  }

  &__flex2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 32px;
  }

  &__tovars {
    padding-top: 24px;
  }
}

// найдено
.result-text {
  span {
    font-weight: 500;
    color: var(--color-primary_active);
  }
}

@media (max-width: 1024px) {
  .catalog__grid {
    display: block;
  }

  .catalog .podcategories {
    margin-bottom: 24px;
  }
}

@media (max-width: 600px) {
  .catalog .page-title {
    margin-bottom: 16px;
  }

  .catalog .podcategories__item {
    padding: 0 12px !important;
    background: none;
    border: none;
  }

  .catalog .podcategories__item:not(:last-child) {
    margin: 0;
    padding: 0;
  }

  .catalog .result-text {
    display: none;
  }
}

@media (max-width: 480px) {
  .catalog .podcategories {
    flex-direction: column;
  }

  .catalog .podcategories__item {
    width: 100%;
    display: block;
    text-align: left;
    position: relative;
    .bx {
      position: absolute;
      top: 10px;
      right: 8px;
    }
    .dropdown {
      width: 100%;
      max-width: 100%;
    }
  }

  .catalog__flex {
    flex-direction: column;
  }

  .catalog__flex .left-block,
  .catalog__flex .right-block {
    width: 100%;
  }

  .catalog__flex .right-block {
    width: 100%;
    align-items: center;
    justify-content: flex-start;
    padding-top: 24px;
    padding-bottom: 16px;
  }

  .catalog__tovars {
    padding-top: 0;
  }

  .catalog__flex2 {
    padding-top: 24px;
    flex-direction: column;

    .pagination {
      order: -1;
      margin-bottom: 16px;
    }
  }
}
