.reviews {
  padding: 48px 0;
  padding-top: 24px;

  &__h2 {
    &--num {
      font-size: 24px;
      font-weight: 400;
      color: var(--color-text-gray);
    }
  }

  &__btns {
    .btn {
      &:not(:last-child) {
        margin-right: 16px;
      }
    }
  }
}

// отзыв
.reviews-item {
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-gap: 32px;

  &:not(:last-child) {
    border-bottom: 1px dashed rgba(0, 0, 0, 0.2);
    padding-bottom: 32px;
    margin-bottom: 32px;
  }

  &__rating {
    margin-bottom: 16px;

    .rating-result span {
      font-size: 18px;
    }
  }

  &__user {
    color: var(--color-title);
    font-weight: 500;
    margin-bottom: 0;
  }

  &__date {
    @extend .news__item--date;
    font-size: 14px;
    margin-bottom: 10px;

    .bx {
      font-size: 16px;
    }
  }
}

// опрос
.opros {
  &__title {
    @extend .reviews-item__user;
    margin-bottom: 4px;
  }

  &__result {
    display: flex;
    align-items: center;
  }

  &__btn {
    cursor: pointer;
    display: flex;
    align-items: center;
    color: var(--color-text-gray);
    transition: all 0.2s ease-in-out;

    .bx {
      margin-right: 6px;
    }

    &:hover {
      transform: scale(1.09);
      color: var(--color-body);
    }

    &--like:hover .bx {
      color: var(--color-primary);
    }

    &--dislike:hover .bx {
      color: var(--color-accent-red);
    }
  }
}

// отзыв - преимущества
.review-features {
  margin-bottom: 24px;

  li {
    strong {
      font-weight: 500;
      color: var(--color-title);
    }

    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
}

.review {
  &__title {
    font-size: 16px;
    font-weight: 500;
  }
}

// комментарий
.review-comment {
  &__link {
    @extend .more-link;
    color: var(--color-primary_active);
    font-weight: 500;
    font-size: 14px;
  }

  &__textarea {
    width: 100%;
    min-height: 100px;
    margin-bottom: 16px;
  }

  .form {
    display: none;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 24px;

    .form__btn {
      width: 130px;
      margin-bottom: 0;
    }

    &.is-active {
      display: flex;
    }
  }
}

@media (max-width: 800px) {
  .reviews {
    padding-top: 16px;
  }

  .reviews-item {
    display: block;
  }
}

@media (max-width: 480px) {
  .reviews {
    padding: 32px 0;
    padding-top: 0;
  }

  .reviews__h2 {
    font-size: 24px;
  }

  .reviews-item__user,
  .revuews-item .opros__title,
  .review-features li {
    font-size: 14px;
  }

  .review-features {
    margin-top: 16px;
  }

  .review-comment__text p {
    font-size: 14px;
    line-height: 1.6;
  }

  .review-comment .form .form__btn {
    width: 100%;
  }

  .reviews__btns .btn:not(:last-child) {
    margin-right: 0;
    margin-bottom: 16px;
  }
}
