.tovar {
  &__head {
    width: 100%;
    max-width: 860px;
    margin-bottom: 24px;
  }

  &__inner-top {
    display: grid;
    grid-template-columns: 6fr 6fr;
    grid-gap: 32px;
    align-items: center;
    padding-bottom: 48px;
  }

  &__rating {
    margin-bottom: 16px;
    // margin-right: 16px;
  }

  &__name {
    color: var(--color-title);
    font-size: 36px;
    font-weight: 400;
    line-height: 1.2;
    margin-bottom: 16px;
    margin: 16px 0;
  }

  // завод номер + артикул
  &__flex {
    display: flex;
    align-items: center;
    // margin-bottom: 32px;

    .articul {
      padding-right: 16px;

      &::after {
        content: "|";
        position: relative;
        right: -6px;
        opacity: 0.4;
      }
    }
  }

  &__h3 {
    font-weight: 500;
    font-size: 20px;
    margin-bottom: 16px;
  }

  // &__info-2 {
  //   margin-bottom: 24px;
  // }

  // список с параметрами
  .list-info {
    margin-bottom: 24px;

    &__item {
      color: var(--color-body);

      &--name {
        color: var(--color-text-gray);
      }

      &:first-child a {
        color: var(--color-primary_active);
      }

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }

    a {
      display: inline-block;
      color: var(--color-body);
      text-decoration: none;

      &:hover {
        color: var(--color-primary_active);
        text-decoration: underline;
      }

      &:not(:last-child) {
        padding-right: 6px;

        &::after {
          content: ",";
          position: relative;
          right: -2px;
          color: var(--color-body);
        }
      }
    }
  }

  // label подходит
  .tovar__label {
    margin-bottom: 24px;

    &--text {
      display: inline-block;
      padding: 8px 16px;
      font-size: 16px;
      line-height: 1.2;
      border: 1px solid var(--color-primary);
      border-radius: 4px;
    }

    a {
      text-decoration: none;

      &:hover {
        color: var(--color-primary_active);
      }
    }
  }

  // ссылка подробнее
  &__more-link {
    @extend .more-link;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
  }

  // блок с описанием
  &__textinfo {
    padding: 48px 0;

    &--grid {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 32px;
    }
  }
}

// галлерея
.tovar-gallery {
  display: flex;
  align-items: center;

  &__thumbs {
    display: flex;
    flex-direction: column;
    margin-right: 32px;
  }

  &__thumbs--item {
    cursor: pointer;
    width: 80px;
    height: 80px;
    overflow: hidden;
    opacity: 0.6;

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &.is-active {
      border: 1.5px solid var(--color-primary);
      opacity: 1;
    }
  }

  &__thumbs--img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__img-active {
    cursor: pointer;
    position: relative;
    transition: all 1s ease;
    overflow: hidden;
  }
}

// артикул
.articul {
  color: var(--color-text-gray);

  &__num {
    color: var(--color-body);
  }
}

// заводской номер
.zavodnumber {
  color: var(--color-text-gray);

  &__num {
    color: var(--color-body);
  }
}

// блок с описанием
.textinfo {
  padding: 24px;
  border: 1px solid var(--color-border-dark);
  border-radius: 8px;

  &__title {
    color: var(--color-title);
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 16px;
  }

  p strong {
    font-weight: 500;
  }

  &__links {
    a {
      cursor: pointer;
      text-decoration: none;

      &:hover {
        color: var(--color-primary_active);
        text-decoration: underline;
      }

      &::after {
        content: ",";
      }
    }
  }

  &__readmore {
    @extend .more-link;
    font-size: 16px;
    line-height: 1;
  }
}

// Медиа запросы
@media (max-width: 1024px) {
  .tovar__inner-top {
    grid-template-columns: 5fr 7fr;
  }

  .tovar-gallery {
    flex-direction: column;
  }

  .tovar-gallery__img-active {
    margin-bottom: 16px;
  }

  .tovar-gallery__thumbs {
    width: 100%;
    flex-direction: row;
    justify-content: center;
    order: 2;
    margin-right: 0;
  }

  .tovar-gallery__thumbs--item {
    width: 64px;
    height: 64px;
  }

  .tovar-gallery__thumbs--item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 8px;
  }

  .tovar__name {
    font-size: 32px;
  }

  .tovar__textinfo--grid {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 24px;
  }

  .tovar__textinfo {
    padding-bottom: 32px;
  }
}

@media (max-width: 800px) {
  .tovar__inner-top {
    grid-template-columns: 1fr 2fr;
  }

  .tovar__info .rating-result span {
    font-size: 20px;
  }

  .tovar__name {
    font-size: 28px;
  }

  // .tovar__flex {
  //   margin-bottom: 24px;
  // }

  // .tovar__info-2 {
  //   margin-bottom: 16px;
  // }

  .tovar .tovar__label {
    &--text {
      font-size: 14px;
      padding: 8px 16px;
    }
  }

  // .tovar__h3 {
  //   font-size: 18px;
  //   margin-bottom: 8px;
  // }

  .tovar__list-info li:not(:last-child) {
    margin-bottom: 8px;
  }
}

@media (max-width: 600px) {
  .tovar__inner-top {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-gap: inherit;

    .tovar-gallery {
      width: 100%;
      margin-bottom: 32px;
    }
  }

  .tovar__head {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 24px;
  }

  .tovar-gallery__img-active {
    width: 40%;
  }

  .tovar__name {
    font-size: 24px;
    margin-top: 0;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .tovar__inner-top {
    padding-bottom: 32px;
  }
  .tovar-gallery {
    margin-bottom: 16px;
  }
  .tovar-gallery__thumbs--item {
    width: 48px;
    height: 48px;
  }

  // .tovar__rating {
  //   display: flex;
  //   justify-content: center;
  // }

  .tovar__name {
    font-size: 20px;
    line-height: 1.4;
    font-weight: 500;
    text-align: center;
  }

  .tovar__flex {
    justify-content: center;
  }

  .tovar__info {
    width: 100%;
  }

  .tovar .tovar__label--text {
    width: 100%;
    padding: 8px;
    text-align: center;
  }

  .tovar .list-info__item:not(:last-child) {
    margin-bottom: 8px;
  }

  .textinfo {
    padding: 0;
    border: none;
  }

  .textinfo__title {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 8px;
  }

  .tovar__textinfo {
    padding: 32px 0;
  }
}

@media (max-width: 359px) {
  .tovar__name {
    font-size: 18px;
  }
}
