/* Стиль списка */
.breadcrumb {
  &__list {
    list-style: none;
    padding: 24px 0;
  }

  &__item {
    display: inline;
    font-size: 15px;
  }

  &__link {
    color: var(--color-text-gray);
    font-size: 15px;
    line-height: 1.4;
    text-decoration: none;
    transition: all 0.2s ease-in-out;
    &:hover {
      color: var(--color-primary_active);
      text-decoration: underline;
      opacity: 1;
    }
    &.active {
      color: var(--color-body);
      text-decoration: none !important;
    }
  }

  // стрелка
  &__item + li::before {
    content: "›";
    color: rgba(#000, 0.6);
    font-size: 18px;
    display: inline-block;
    padding: 0 4px;
  }
}

@media (max-width: 800px) {
  .breadcrumb__list {
    padding: 16px 0;
    padding-bottom: 24px;
  }
}

@media (max-width: 480px) {
  .breadcrumb__list {
    padding: 16px 0;
  }

  .breadcrumb__item,
  .breadcrumb__link {
    font-size: 14px;
  }
}
